.left-side {
    padding-top: 15vh;
    grid-column: 1 / 9;
    /* grid-row: span 7; */
    line-height: 900%;
    align-items: center;
}

.left-side h2 {
    padding-top: 5%;
    padding-left: 2%;
    font-size: 72px;
    font-weight: 800;
}

.left-side h1 {
    letter-spacing: 35px;
    font-size: 233px;
    font-family: "Roboto", sans-serif;
    font-weight: 800;
    /* margin-right: 25%; */
}

.left-side p {
    font-size: 40px;
    font-weight: 800;
    /* padding-left: 2%; */
}

hr {
    padding-top: 10%;
}

.line {
    border-bottom: 3px solid rgb(0, 0, 0);
}

.hi {
    background-color: #233446;
    /* background-color: #151f2a; */
    color: #dad6ca;
}

/* RIGHT SIDE */
.right-side {
    /* padding-left: 20vh; */
    padding-top: 25vh;
    grid-column: 9/ 13;
    align-items: center;
    color: black;
}


.right-side h2 {
    font-size: 50px;
    font-weight: 800;
}

.right-side-h2-shadows {
    /* text-shadow: 10px 10px white; */
}

.solutions-text {}

hr {
    border: none;
    margin-top: 0;
    border-bottom: 1px solid black;
    /* box-shadow: 0 20px 20px -20px #333; */
    margin: -50px auto 10px;
}


@media (max-width: 93em) {
    .left-side {
        line-height: 700%;
    }

    .right-side {
        padding-top: 0vw;



        line-height: 900%;
        align-items: center;
        display: flex;
        flex-direction: row;

        gap: 20px;

    }

    hr {
        opacity: 0;
    }

    .left-side p {}
}