.splash-grid-container {
    grid-column: 1 / 13;
    grid-row: span 7;

    background-color: #dad6ca;
    color: black;
    padding: 20px;
    min-width: 1200px;
    padding-left: 10vw;
    padding-right: 10vw;

    display: grid;
    grid-template-columns: repeat(12, 1fr);
}

@media (max-width: 116em) {
    .splash-grid-container {
        /* background-color: red; */
        padding-left: 0vw;
        padding-right: 0vw;
    }
}

@media (max-width: 93em) {
    .splash-grid-container {
        /* background-color: yellow; */
        padding-left: 3vw;
        padding-right: 3vw;
        grid-row: span 7;

        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
    }
}

.work-grid-container {
    grid-column: 1 / 13;
    grid-row: span 7;

    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-auto-rows: 100px;
    gap: 20px;
    max-width: 1200px;
    margin: 60px auto;
    padding: 20px;
    min-width: 1200px;
}

.project-grid-container {
