.section-title {
    grid-column: span 12;
    grid-row: span 2;
    display: grid;
    align-items: center;
    justify-content: center;
    font-optical-sizing: auto;
    font-style: normal;
    font-size: 72px;
    border-radius: 5px;

    letter-spacing: 12px;
    font-size: 100;
    font-family: "Roboto", sans-serif;
    font-weight: 300;
}

